class App extends React.Component {
  constructor(props) {
    super(props);

    // set user id in cookie
    const cookies = new Cookies();
    let userID = cookies.get("userID");
    if (userID === undefined) { // if no user id
      userID = uuidToHex(uuid4()); // generate user id
      cookies.set('userID', userID, {
        maxAge: (60 * 60 * 24), // one day
        path: '/'
      });
      console.log("<App> detected new user")
      console.log("<App> assigned uid: " + userID)
    } else {
      console.log("<App> detected uid: " + userID)
    }
    console.log("<App> logged in as: %c" + userID, "background: yellow");

    // get data from local storage
    let server = window.localStorage.getItem("server");
    if (server == null) {
      server = "http://localhost:5000/";
      window.localStorage.setItem("server", server);
    }
    let queryServer = window.localStorage.getItem("queryServer");
    if (queryServer == null) {
      // queryServer = "https://query.wikidata.org/sparql";
      queryServer = "http://sitaware.isi.edu:8080/bigdata/namespace/wdq/sparql";
      window.localStorage.setItem("queryServer", queryServer);
    }

    // init app
    this.state = {
      showSettings: false,

      // temp in form
      tempServer: server,
      tempQueryServer: queryServer,
    };
    window.userID = userID;
    window.server = server;
    window.queryServer = queryServer;
    window.isCellSelectable = false;
    // window.onbeforeunload = () => { return true; }; // prevent leave/reload page
  }

  render() {
    return (
      <div>
        {this.renderSettings()}

        {/* navbar */}
        <div>
          <Navbar className="shadow" bg="dark" variant="dark" sticky="top" style={{ height: "50px" }}>

            {/* logo */}
            <Navbar.Brand className="pl-2 font-weight-bold" style={{ cursor: "help" }} title="Table to Wikidata Mapping Language">
              T2WML&nbsp;GUI
            </Navbar.Brand>
            <Navbar.Text className="pr-2">
              v1.1&nbsp;(beta)
            </Navbar.Text>

            {/* settings */}
            <Navbar.Text className="pr-2 ml-auto" style={{ verticalAlign: "center" }}>
              <Button
                className="d-inline-block float-right"
                variant="outline-light"
                size="sm"
                style={{ padding: "0rem 0.5rem" }}
                onClick={this.handleShowSettings.bind(this)}
              >Settings</Button>
            </Navbar.Text>

          </Navbar>
        </div>

        {/* content */}
        <div>
          <SplitPane className="p-3" split="vertical" defaultSize="60%" minSize={300} maxSize={-300} style={{ height: "calc(100vh - 50px)", background: "#f8f9fa" }}>
            <TableViewer />
            <SplitPane className="" split="horizontal" defaultSize="60%" minSize={200} maxSize={-200}>
              <YamlEditor />
              <Output />
            </SplitPane>
          </SplitPane>
        </div>
      </div >
    );
  }

  renderSettings() {
    return (
      <Modal show={this.state.showSettings} size="lg" onHide={() => {/* do nothing */ }}>

        {/* header */}
        <Modal.Header style={{ background: "whitesmoke" }}>
          <Modal.Title>Settings</Modal.Title>
        </Modal.Header>

        {/* body */}
        <Modal.Body>
          <Form className="container">

            {/* user id */}
            <Form.Group as={Row} style={{ marginTop: "1rem" }}>
              <Form.Label column sm="12" md="3" className="text-right">
                User&nbsp;ID
              </Form.Label>
              <Col sm="12" md="9">
                <Form.Control type="text" defaultValue={window.userID} readOnly />
              </Col>
            </Form.Group>

            {/* server */}
            <Form.Group as={Row} onChange={(event) => this.setState({ tempServer: event.target.value })}>
              <Form.Label column sm="12" md="3" className="text-right">
                Server
              </Form.Label>
              <Col sm="12" md="9">
                <Form.Control type="url" defaultValue={window.server} />
              </Col>
            </Form.Group>

            {/* query server */}
            {/* <Form.Group as={Row} onChange={(event) => this.setState({ tempQueryServer: event.target.value })}>
              <Form.Label column sm="12" md="3" className="text-right">
                SPARQL&nbsp;Query
              </Form.Label>
              <Col sm="12" md="9">
                <Form.Control type="url" defaultValue={window.queryServer} />
              </Col>
            </Form.Group> */}

            {/* query server */}
            <Form.Group as={Row}>
              <Form.Label column sm="12" md="3" className="text-right">
                SPARQL&nbsp;Query
              </Form.Label>
              <Col sm="12" md="9">
                <Form.Control as="select"
                  onChange={(event) => this.setState({ tempQueryServer: event.target.value })}
                  defaultValue={window.queryServer}
                >
                  <option value="http://sitaware.isi.edu:8080/bigdata/namespace/wdq/sparql">http://sitaware.isi.edu:8080/bigdata/namespace/wdq/sparql</option>
                  <option value="https://query.wikidata.org/sparql">https://query.wikidata.org/sparql</option>
                </Form.Control>
              </Col>
            </Form.Group>

          </Form>
        </Modal.Body>

        {/* footer */}
        <Modal.Footer style={{ background: "whitesmoke" }}>
          <Button variant="outline-dark" onClick={this.handleHideSettings.bind(this)}>
            Cancel
          </Button>
          <Button variant="dark" onClick={this.handleDoSettings.bind(this)}>
            Save
          </Button>
        </Modal.Footer>

      </Modal>
    );
  }

  handleDoSettings(event) {
    console.log("<App> updated settings");
    window.server = this.state.tempServer;
    window.localStorage.setItem("server", window.server);
    window.queryServer = this.state.tempQueryServer;
    window.localStorage.setItem("queryServer", window.queryServer);
    this.setState({ showSettings: false });
  }

  handleHideSettings(event) {
    this.setState({ showSettings: false });
  }

  handleShowSettings(event) {
    this.setState({ showSettings: true });
  }
}

